/* Import Georgia Pro font if available, fallback to Georgia and serif */
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500;600;700&display=swap');

/* Reset default margins and padding */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    width: 100%;
    overflow-x: hidden;
}

/* Custom header styling with Georgia Pro / EB Garamond */
h1, h2, h3 {
    font-family: 'EB Garamond', 'Georgia Pro', Georgia, serif !important;
    font-weight: 600;
}

/* Briglia Hundley Banner Styles */
.gdlr-page-title-wrapper {
    position: relative;
    text-align: center;
    color: white;
    min-height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 60px 0;
}

.gdlr-page-title-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1;
}

/* Briglia Hundley Practice Areas Styles */
.gdlr-practice-thumbnail {
    position: relative;
    overflow: hidden;
    border-radius: 8px;
    cursor: pointer;
}

.gdlr-practice-thumbnail img {
    transition: transform 0.3s ease;
}

.gdlr-practice-thumbnail:hover img {
    transform: scale(1.05);
}

.gdlr-practice-thumbnail-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.6) 100%);
    z-index: 1;
    transition: opacity 0.3s ease;
}

.gdlr-practice-thumbnail:hover .gdlr-practice-thumbnail-overlay {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
}

.practice-title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1.5rem;
    color: white;
    font-size: 1.25rem;
    font-weight: 700;
    z-index: 2;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
    transition: all 0.3s ease;
}

.practice-title a {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
}

.practice-title a:hover {
    color: #e5e7eb;
}

.gdlr-item.gdlr-modern-practice {
    margin-bottom: 2rem;
}

.gdlr-modern-practice-ux {
    opacity: 1;
    padding-top: 0px;
    margin-bottom: 0px;
}

.gdlr-page-title-container {
    position: relative;
    z-index: 2;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.gdlr-page-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: white;
    margin-bottom: 0.5rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

@media (max-width: 768px) {
    .gdlr-page-title-wrapper {
        min-height: 200px;
        padding: 40px 0;
    }
    .gdlr-page-title {
        font-size: 1.8rem;
    }
}

.App {
    text-align: center;
    margin: 0;
    padding: 0;
    width: 100%;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #0f0f10;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
